<template>
  <div class="login-section">
    <div v-if="isShowWechat" style="width:100%;">
      <div class="root_header">
        <img class="icon_logo" src="../../assets/img/icon_banner_01.png" alt="" />
      </div>

      <div class="main_view">
        <div class="main">
          <div class="icon_login_text"></div>
          <div class="main_item_div">
            <div class="main_item_text"></div>
            <div class="form-item">
              <input
                v-model="phone"
                type="text"
                name="phone"
                :oninput="phone = (phone.length > 11 ? phone.slice(0, 11) : phone)"
                placeholder=""
                maxlength="11"
              />
              <!-- <div class="get_code" @click="btnClick">
                {{ codeRestTime ? `${codeRestTime}S` : "" }}
              </div> -->
            </div>
          </div>
          <div class="main_item_div">
            <div class="main_item_text"></div>
            <div class="form-item">
              <input
                v-model="code"
                type="password"
                name="code"
                placeholder=""
                maxlength="11"
              />
              <!-- :oninput="code = (code.length > 6 ? code.slice(0, 6) : code)" -->
            </div>
          </div>
        </div>

         <van-button class="submit_view" block type="info" @click="commit"></van-button>
      </div>
    </div>

    <div class="login-section" v-else>
      <div class="icon_login_content">,</div>
    </div>
  </div>
  
</template>

<script>
import { getLogin } from "../../request/api";
import { Notify,Toast } from 'vant';

export default {
  name: "",
  components: {},
  data() {
    return {
      code: "", // 
      phone: "", // 
      codeRestTime: 0,
      restTime: 60,
      phoneCode: ["", ""], //   
      phoneSub: "",
      codeSub: "",
      isShowWechat: true
    };
  },
  created() {},
  mounted() {
    // this.isWechat();
    this.getAutoLogin();
  },
  methods: {
    isWechat() {
      const ua = window.navigator.userAgent.toLowerCase();
      if (ua.match(/micromessenger/i) == 'micromessenger') {
        console.log('');
        this.isShowWechat = true;
        this.getAutoLogin();
        return true;
      } else {
        this.isShowWechat = false;
        console.log(',');
        return false;
      }
    },
    checkPhone(phone) {
      var phoneReg = /^1\d{10}$/;
      if (phone.length != 11) {
        return false;
      } else if (!phoneReg.test(phone)) {
        return false;
      } else {
        return true;
      }
    },
    btnClick() {
      let that = this;
      if (this.phone == "") {
        Toast('')
          // Notify({ type: 'danger', message: ''});
        // alert("");
      } else if (!this.checkPhone(this.phone)) {
        Toast('')
          // Notify({ type: 'danger', message: ''});
        // alert("");
      } else {
        this.codeRestTime = this.restTime;
        let timer = setInterval(() => {
          --this.codeRestTime;
          if (!this.codeRestTime) {
            clearInterval(timer);
            timer = null;
          }
        }, 1000);

        getVerification({
          phone: this.phone,
        }).then((res) => {
          if (res.data.success) {
              console.log(res.data.msg)
              that.phoneSub = res.data.phone;
              that.codeSub = res.data.msg;
            // that.phoneCode[(that.phone, res.msg)];
          } else {
            // alert("");
            Toast(res.data.msg)
            // Notify({ type: 'primary', message:  res.msg});
          }
        });
      }
    },
    commit() {
      let that = this;
      if (this.phone == "") {
        Toast('')
          //  Notify({ type: 'danger', message: ''});
        return;
      // } else if (!this.checkPhone(this.phone)) {
      //   Toast('')
      //   return;
      } else if (this.code == "") {
        Toast('')
          // Notify({ type: 'danger', message: ''});
        return;
      // } else if (this.code == "") {
      //   Toast('')
      //     // Notify({ type: 'danger', message: ''});
      //   return;
      // } else if (this.phoneSub == "") {
      //   Toast('')
      //   // Notify({ type: 'danger', message: ''});
      //   return;
      // } else if (this.phoneSub != this.phone) {
      //   Toast('')
      //     // Notify({ type: 'danger', message: ''});
      //   return;
      // } else if (this.codeSub != this.code) {
      //   Toast('')
      //     // Notify({ type: 'danger', message: ''});
      //   return;
      } else {
        getLogin({
          person_phone: this.phone,
          password: this.code
        }).then((res) => {
          console.log(res)
          if (res.data.success) {
            Toast(res.data.msg)
            //allow_auto_login 0-  1-
            localStorage.setItem('lisPhone',that.phone);
            localStorage.setItem('lisPassword',that.code)
            if(res.data.allow_auto_login == 1 || res.data.allow_auto_login == 0){
              that.$router.push({
                  path: "/lisInfoCustom",
                  query:{id: res.data.result[0].id}
              });
            }
          } else {
            Toast(res.data.msg)
          }
        });
      }
    },
    handleLogin() {
      this.$router.push({
        path: "/lisInfoCustom",
        query:{id: '1'}
      });
    },
    getAutoLogin(){
      let that = this;
      let phone = localStorage.getItem('lisPhone');
      let password = localStorage.getItem('lisPassword');
      if(phone && password){
        // this.phone = phone;
        // this.code = password;
        getLogin({
          person_phone: phone,
          password: password
        }).then((res) => {
          console.log(res)
          if (res.data.success) {
            Toast(res.data.msg)
            //allow_auto_login 0-  1-
            if(res.data.allow_auto_login == 0){
              that.$router.push({
                  path: "/lisInfoCustom",
                  query:{id: res.data.result[0].id}
              });
            }
          } else {
            Toast(res.data.msg)
          }
        });
      }
    }
  },
};
</script>
